.footer {
  padding: 5rem;
  background: $color-dark-grey-3;
  font-size: 1.7rem;

  &__logo-box {
    text-align: center;
  }

  &__logo {
    width: 20rem;
  }

  &__navigations {
      border-top: 2px solid $color-dark-grey-2;
      display:inline-block;
      padding-top: 3rem;
    }
    &__copyright{
        display:inline-block;
        width:80%;
        text-align: right;
        float:right;
        border-top: 2px solid $color-dark-grey-2;
        display:inline-block;
        padding-top: 3rem;
        
  }

  &__item {
    display: inline-block;
    list-style: none;
    &:not(:last-child) {
      margin-right: 2rem;
    }
  }

  &__link {
    &:link,
    &:visited {
      display: inline-block;
      text-transform: uppercase;
      text-decoration: none;
      color: $color-light-grey;
      transition: all .2s;
      background: $color-dark-grey-3;
    }

    &:hover,
    &:active {
      transform: rotate(8deg) scale(1.2);
      color: $color-primary;
      box-shadow: 1rem 1rem 1rem rgba($color-black, 0.3);
    }
  }
}
